html, body {
  font-family: Calibri, sans-serif;
  font-size: 1rem;
  height: 100%;
  margin: 0;
  padding: 0;
  background-color: #dfdfdf;
}

.bio-box {
  margin: auto;
  margin-top: 20px;
  width: 30%;
  background-color: #202020;
  border-radius: 5px;
  padding: 50px 10px 55px 10px;
  animation: fade 1.5s ease-in forwards;
}

@keyframes fade {
  0% {
    opacity: 0;
  }
}

@media (max-width: 768px) {
  .bio-box {
    margin: auto;
    margin: 1% 1% 0 1%;
    box-sizing: border-box;
    width: 98%;
    background-color: #202020;
    border-radius: 5px;
    padding: 50px 10px 55px 10px;
  }
}

.bio {
  color: white;
  text-align: center;
  max-height: 100%;
  max-width: 100%;
}

#image {
  border-radius: 50%;
  max-width: 35%;
  max-height: 35%;
  content: url(./images/daniel.jpg)
}

#name {
  margin-top: 5%;
  font-size: 1.3rem;
}

#title-master {
  margin-top: 5%;
  font-size: .9rem;
}

#title-bachelor {
  font-size: .9rem;
  margin-top: 5%;
}

.links-container {
  margin: auto;
  margin-top: 10%;
  max-width: 50%;
  display: flex;
  justify-content: space-evenly;
  gap: 10px;
}

.masters-info img {
  width: 90px;
  height: 90px;
}

.masters-info a {
  margin: 10px;
}

.footer {
  margin-top: 20px;
  width: 100%;
  text-align: center;
  font-size: 0.7rem;
}

.academics {
  margin: 1% 1% 0 1%;
}

.masters {
  background-color: #202020;
  height: fit-content;
  margin-top: 1%;
  color: white;
  border-radius: 5px;
}

.projects {
  background-color: #202020;
  color: white;
  border-radius: 5px;
}

.projects-info {
  margin: 10px;
  overflow: hidden;
}

.projects .associated {
  display: flex;
  align-items: center;
}

.projects img {
  width: 20px;
  height: 20px;
  margin-right: 5px;
}

.projects #date, .projects h1 {
  margin: 0;
  margin-top: 10px;
}

.masters-info {
  display: flex;
  align-items: flex-start;
}

#school {
  font-size: 1rem;
}

#course {
  font-size: .9rem;
}

#date {
  font-size: .8rem;
}

h1 {
  font-size: 1.3rem;
}

#link {
  width: 40px;
  height: 40px;
}

#link:hover {
  border-radius: 50%;
  box-shadow: 0 8px 16px 0 rgba(255, 255, 255, 0.2);
}

.skills {
  background-color: #202020;
  overflow: hidden;
  display: flex;
  justify-content: space-evenly;
  gap: 10px;
  border-radius: 5px;
  flex-wrap: wrap;
}

.circular-chart {
  display: block;
  margin: 10px auto;
  max-width: 80%;
  max-height: 80px;
}

.circle {
  stroke: #dfdfdf;
  fill: none;
  stroke-width: 2.8;
  stroke-linecap: round;
  animation: progress 1.5s ease-out forwards;
}

@keyframes progress {
  0% {
    stroke-dasharray: 0 100;
  }
}

.languages {
  background-color: #202020;
  border-radius: 5px;
  color: white;
  overflow: hidden;
}

.languages-info {
  margin: 10px;
}

.driving-licence {
  border-radius: 5px;
  background-color: #202020;
  color: white;
  overflow: hidden;
}

.driving-licence-info {
  margin: 10px;
}